.paginator {
    display: grid;
}

.content {
    margin: 0 auto;

}

.pageBtn {
    color: #7a7a7b;
    background-color: #444446;
    padding: 5px 10px;
    vertical-align: top;
    cursor: pointer;
    outline: none;
    border: none;
}

.selectedPage {
    color: #7a7a7b;
    background-color: #333336;
    padding: 5px 10px;
    vertical-align: top;
    font-weight: 700;
    cursor: pointer;
    outline: none;
    border: none;

}
